  background-color: rgba(0, 0, 0, 0.5);
  display: none; }
  #checkQWrapper #checkQ {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    right: 0;
    margin: 0 auto;
    width: 300px;
    background-color: #f1f3f4;
    border-radius: 5px; }
    #checkQWrapper #checkQ > div {
      overflow: hidden; }
